export default "<template>\n  <Sandbox\n    id=\"example\"\n    title=\"Example\"\n  >\n    <Preview>\n      <VTable\n        :headers=\"headers\"\n        :items=\"items\"\n        :bordered=\"bordered\"\n        :elevated=\"elevated\"\n        :rounded=\"rounded\"\n        :rounded-lg=\"roundedLg\"\n        :dark=\"dark\"\n        :light=\"light\"\n        :fixed-header=\"fixedHeader\"\n        :expandable=\"expandable\"\n        :multiple-expand=\"multipleExpand\"\n        :padding=\"padding\"\n        :hide-parent-row-on-expand=\"hideParentRowOnExpand\"\n        :expand-on-row-click=\"expandOnRowClick\"\n        style=\"height: 300px;\"\n      >\n        <template #expanded-item=\"{item}\">\n          <div\n            style=\"width: 100%;\"\n            class=\"has-padding-lg\"\n          >\n            {{ item.name }}\n          </div>\n        </template>\n      </VTable>\n    </Preview>\n    <template #controls>\n      <Knobs>\n        <KnobBoolean\n          v-model=\"dark\"\n          label=\"Dark\"\n        />\n        <KnobBoolean\n          v-model=\"light\"\n          label=\"Light\"\n        />\n        <KnobBoolean\n          v-model=\"bordered\"\n          label=\"Bordered\"\n        />\n        <KnobBoolean\n          v-model=\"elevated\"\n          label=\"Elevated\"\n        />\n        <KnobBoolean\n          v-model=\"rounded\"\n          label=\"Rounded\"\n        />\n        <KnobBoolean\n          v-model=\"roundedLg\"\n          label=\"Rounded large\"\n        />\n        <KnobBoolean\n          v-model=\"fixedHeader\"\n          label=\"Fixed header\"\n        />\n        <KnobBoolean\n          v-model=\"expandable\"\n          label=\"Expandable\"\n        />\n        <KnobBoolean\n          v-model=\"multipleExpand\"\n          label=\"Multiple expand\"\n        />\n        <KnobBoolean\n          v-model=\"hideParentRowOnExpand\"\n          label=\"Hide parent row on expand\"\n        />\n        <KnobBoolean\n          v-model=\"expandOnRowClick\"\n          label=\"Expand on row click\"\n        />\n        <KnobListItem\n          v-model=\"padding\"\n          label=\"Padding\"\n          :items=\"spacings\"\n        />\n      </Knobs>\n    </template>\n  </Sandbox>\n</template>\n\n<script>\nimport {\n  Preview,\n  Sandbox,\n  Knobs,\n  KnobBoolean,\n  KnobListItem\n} from '@/docs/components';\nimport {\n  VTable\n} from '@/components';\nimport { spacings } from '@/docs/helpers/story-params';\n\nexport default {\n  name: 'TableExample',\n  components: {\n    Preview,\n    Sandbox,\n    Knobs,\n    KnobBoolean,\n    KnobListItem,\n    VTable\n  },\n  data () {\n    return {\n      headers: [\n        {\n          name: 'id',\n          text: 'Id'\n        },\n        {\n          name: 'name',\n          text: 'Name'\n        },\n        {\n          name: 'age',\n          text: 'Age'\n        }\n      ],\n      items: [\n        {\n          id: '1',\n          name: 'John Doe',\n          age: 35\n        },\n        {\n          id: '2',\n          name: 'Martin Doyle',\n          age: 48\n        },\n        {\n          id: '3',\n          name: 'Adam Smith',\n          age: 23\n        },\n        {\n          id: '4',\n          name: 'John Doe',\n          age: 35\n        },\n        {\n          id: '5',\n          name: 'Martin Doyle',\n          age: 48\n        },\n        {\n          id: '6',\n          name: 'Adam Smith',\n          age: 23\n        },\n        {\n          id: '7',\n          name: 'John Doe',\n          age: 35\n        },\n        {\n          id: '8',\n          name: 'Martin Doyle',\n          age: 48\n        },\n        {\n          id: '9',\n          name: 'Adam Smith',\n          age: 23\n        },\n        {\n          id: '10',\n          name: 'John Doe',\n          age: 35\n        },\n        {\n          id: '11',\n          name: 'Martin Doyle',\n          age: 48\n        },\n        {\n          id: '12',\n          name: 'Adam Smith',\n          age: 23\n        }\n      ],\n      spacings,\n      bordered: false,\n      elevated: false,\n      rounded: false,\n      roundedLg: false,\n      dark: false,\n      light: false,\n      fixedHeader: false,\n      expandable: false,\n      multipleExpand: false,\n      hideParentRowOnExpand: false,\n      expandOnRowClick: false,\n      padding: 'xs'\n    };\n  }\n};\n</script>\n\n<style lang=\"scss\">\n</style>\n";